.links {

  &__title {
    color: #393939;
    font-family: "Open Sans", sans-serif;
    font-size: 34px;
    font-weight: 400;
    margin-bottom: 45px;
  }

  &__list {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    width: 311px;
    height: 50px;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    flex-grow: 1;
    transition: 0.1s color ease-in-out;
  }

  &__first-link {
    color: #000000;
    font-family: "Source Sans Pro";
    font-size: 16px;

    &:hover {
      color: #ef6c00;
    }
  }

  &__second-link {
    color: #ffffff;
    font-family: "Source Sans Pro";
    font-size: 16px;
    background-color: #283593;

    &:hover {
      color: #ef6c00;
    }
  }

  &__third-link {
    color: #ef6c00;
    font-family: "Source Sans Pro";
    font-size: 22px;

    &:hover {
      color: #fc831f;
    }
  }
}